body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
}

.nav-active {
  color: green;
}

.ProcessIcon svg {
  font-size: 4em;
}
.ProcessTitle {
  font-weight: 600;
  height: 4vh;
}
.ProcessTile {
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.benefitDropdownSelector {
  display: none;
}

.benefitButtonSelector {
  display: flex;
  flex-direction: row;
  width: 70%;
  margin: auto;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.BenefitText {
  height: 35vh;
  background-color: none;
  width: 65%;
  margin: 1% auto;
  min-height: 25vh;
}

@media (max-width: 1040px) {
  .benefitDropdownSelector {
    display: flex;
    margin: auto;
    margin-bottom: 3vh;
    max-width: 80%;
    flex-direction: column-reverse;
  }
  .benefitButtonSelector {
    display: none;
  }
  .BenefitText {
    height: 50vh;
    width: 90vw;
  }
}
@media (max-width: 480px) {
}

.PricingBox {
  width: 25%;
  background-color: white;
  border: 2px solid #d2d4d1;
  min-height: 30vh;
  border-radius: 5px;
  margin: auto 0.5em;
}
.PricingBox li {
  text-align: left;
}
.PricingBox header {
  display: flex;
  justify-content: center;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  width: 100%;
}
.PricingBox h2 {
  margin-top: 1%;
  font-size: 3.5rem;
}
.PricingBox h3 {
  margin-bottom: 1%;
}
footer {
  padding-bottom: 15px;
}

@media (max-width: 600px) {
  .PricingBox {
    width: 80%;
    margin-bottom: 5%;
  }
}

.MemberCard {
  width: 20vw;
  color: white;
}
.MemberCard h2 {
  margin-bottom: 0;
}
.MemberHolder {
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

@media (max-width: 600px) {
  .MemberCard img {
    width: 80%;
  }
  .MemberCard h2 {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    margin-top: 0;
  }
  .MemberCard h4 {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    margin-top: 0;
  }
}

.DemoBar {
  background-color: #a1bd69;
  display: flex;
  flex-direction: row;
  height: 35vh;
  flex-wrap: wrap;
}
.DemoButtonBox {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(/vdollhouse/static/media/wood.16b82e6b.jpg);
}
.DemoDescriptionBox {
  width: 50%;
  height: 100%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: left;
}

@media (max-width: 800px) {
  .DemoButtonBox {
    width: 100%;
    height: 17vh;
  }
  .DemoDescriptionBox {
    width: 100%;
    height: 17vh;
  }
}

.HiringBar {
  background-color: #a1bd69;
  display: flex;
  flex-direction: row;
  height: 35vh;
  flex-wrap: wrap;
}
.HiringButtonBox {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
}
.HiringDescriptionBox {
  width: 50%;
  height: 100%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: left;
}

.HiringButtonBox h1 {
  display: none;
}
@media (max-width: 800px) {
  .HiringBar {
    height: 25vh;
  }
  .HiringDescriptionBox {
    width: 100%;
    height: 17vh;
    display: none;
  }
  .HiringButtonBox {
    display: flex;
    color: #966b16;
    flex-wrap: wrap;
    width: 100%;
    height: 25vh;
  }
  .HiringButtonBox h1 {
    display: flex;
    margin-right: 2%;
  }
}

.Footer {
  margin-top: auto;
  color: rgb(135, 126, 126);
  background-color: rgb(183, 220, 120);
  font-size: smaller;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.FooterContent a {
  text-decoration: none;
  color: green;
}
.FooterContent {
  width: 100%;
  display: flex;
  flex-direction: row;
}
.FooterContent div {
  margin-top: 2%;
  margin-bottom: 1%;
  width: 33%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.FooterContent h5 {
  font-weight: 600;
  font-size: 1.2em;
  margin-bottom: 0;
}
.FooterContent div p {
  width: 40%;
  text-align: left;
}

.FooterContent:nth-child(1) {
  background-color: white;
}

